<template>
    <div class="container">
        <div class="head_block">
            <router-link class="" to="/about"><button type="button" class="back_button">«</button></router-link>
        </div>
        <div class="content_block">
            <h1>Другие пользователи</h1>
            <div>введите id вользователя от 1 до 10</div>
            <div>
                <div>
                    <input type="number"
                    class="form_text"
                    placeholder="1-10"
                    v-model="user"
                    style="margin-bottom: 10px;"
                    >
                    <button
                    type="button"
                    class="main_button api_button"
                    v-if="user<11"
                    @click="add"
                    >спроси</button>
            </div>
            <div class="api_info"
            v-for="t in table"
            :key="t">
                <p>id: {{t.id}}</p>
                <p>name: {{t.name}}</p>
                <p>user name: {{t.userName}}</p>
                <p>email: {{t.email}}</p>
                <button
                 type="button"
                 @click="tableDelete(t)"
                 class="main_button api_button">удалить</button>
            </div>
            </div>
        </div>
        <div class="btn_block">
            <router-link to="/chat">
                <button type="button" class="main_button">Далее</button>
            </router-link>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            user: "",
            table: [],
        };
    },
    
    methods: {     
        add() {
            const newRequest = {
                id: this.user,
                name: "-",
                userName:"-",
                email: "-"
            };
            this.table.push(newRequest);
            setTimeout(async() =>{
                const f = await fetch('https://jsonplaceholder.typicode.com/users');
                const data = await f.json();
                this.table.find(t => t.id == newRequest.id).name = data[newRequest.id - 1].name;
                this.table.find(t => t.id == newRequest.id).userName = data[newRequest.id - 1].username;
                this.table.find(t => t.id == newRequest.id).email = data[newRequest.id - 1].email;
            }, 500);
            this.user = "";
        },
        tableDelete(tableRemove) {
           this.table = this.table.filter(t => t != tableRemove);
        },
    }
}

</script>

<style>
.api_button {
    color: black;
    background: transparent;
    border: 1px solid rgba(0, 0, 255, 0.308);
    margin: 20px;
    font-size: 16px;  
}
.api_info {
    margin: 0 auto;
    border: 1px solid rgba(0, 0, 255, 0.308);
    margin-bottom: 10px;
    text-align: center;
}
</style>