<template>
    <div class="container">
        <div class="head_block">
            <router-link class="" to="/about"><button type="button" class="back_button">«</button></router-link>
        </div>
        <div class="content_block">
            <h1>Мои чаты</h1>
            <div>
                <div>
                    <input type="text"
                    class="form_text"
                    v-model="chat"
                    style="margin-bottom: 10px;"
                    placeholder="имя чата"
                    >
                    <input type="text"
                    class="form_text"
                    v-model="chatName"
                    style="margin-bottom: 10px;"
                    placeholder="имя чата"
                    >
                    <input type="text"
                    class="form_text"
                    v-model="chatLink"
                    style="margin-bottom: 10px;"
                    placeholder="ссылка на чат"
                    >
                    <button
                    type="button"
                    class="main_button api_button"
                    @click="add"
                    >добавить</button>
            </div>
            <div class="chat"
            v-for="t in table"
            :key="t">
                <div class="chat_block">
                     <img class="avatar" src="@/components/img/ava.png">
                    <div style="max-width: 183px; overflow: hidden;">
                     <p>{{t.chat}}</p>
                     <p>{{t.chatName}}</p>
                    </div>
                    <div>
                     <a :href="t.chatLink"><img style="max-width: 50px; max-height: 50px; border-radius: 10px;" src="https://img.freepik.com/premium-vector/telegram-icon-in-paper-cut-style-social-media-icons_505135-256.jpg" alt=""></a>
                    </div>
                </div>
                <button
                 type="button"
                 @click="tableDelete(t)"
                 class="main_button api_button">удалить</button>
            </div>
            </div>
        </div>
        <div class="btn_block">
            <router-link to="/">
                <button type="button" class="main_button">Далее</button>
            </router-link>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            chat: "",
            chatLink: "",
            table: [],
        };
    },
    created() {
        const tableData = localStorage.getItem("chats");
        if (tableData) {
        this.table = JSON.parse(tableData);
        }
    },
    methods: {    
        add() {
        const newRequest = {
            chat: this.chat,
            chatName: this.chatName,
            chatLink: this.chatLink,
            };
        this.table.push(newRequest);
        localStorage.setItem('chats', JSON.stringify(this.table));
        this.chat = "";
        this.chatName = "";
        this.chatLink = "";
        },
        tableDelete(tableRemove) {
           this.table = this.table.filter(t => t != tableRemove);
           localStorage.setItem('chatsdel', JSON.stringify(this.table));
        },
    }
}

</script>

<style>
.chat_block {
    display: flex;
    justify-content: space-between;
    flex-wrap: nowrap;
}
.api_button {
    color: black;
    background: transparent;
    border: 1px solid rgba(0, 0, 255, 0.308);
    margin: 20px;
    font-size: 16px;  
}
</style>