
<template>
    <div class="container">
        <div class="head_block"><p class="back">«</p></div>
        <div class="title_block" style="text-overflow: ellipsis; white-space: nowrap; max-width: 100%; overflow: hidden;">первая регистрация первая регистрация первая регистрация первая регистрация первая регистрация первая регистрация</div>
        <div class="content_block">
            <div>
                <input class="form_text" placeholder="Имя" :class="{invalid: v$.name.$error}" v-model="name" @blur="v$.name.$touch">
                <div style="color: red" v-if="v$.name.$error">заполните имя</div>
            </div>
            <div>
                <input class="form_text" placeholder="Фамилия" :class="{invalid: v$.name2.$error}" v-model="name2" @blur="v$.name2.$touch">
                <div style="color: red" v-if="v$.name2.$error">заполните фамилию</div>
            </div>
            <div>
                <input class="form_text" placeholder="email" :class="{invalid: v$.email.$error}" v-model="email" @blur="v$.email.$touch">
                <div style="color: red" v-if="v$.email.$error">введите корректную почту</div>
            </div>
            <div class="filter">
              <div class="pos">
                  <div class="all">
                    <p><input type="text" class="filter_input" v-model="filter"></p>
                    <div class="arrow" @click="open"
                    :class="{arrooow: !this.isHide}"
                    ></div>
                  </div>
                  <div class="hideContent accordion" :class="{'accordiooon': isHide == false}">
                    <div v-if="isHide==false">
                      <div class="filter_scrol">
                        <div class="filter_list"
                        v-for="t in filteredList()"
                        :key="t">
                        <p class="click"
                        @click="FilterClick(t)" v-html="matchName(t.name)"></p>
                        <p style="color: blue; font-size: 12px;">{{t.url}}</p>
                        </div>
                      </div>
                    </div>
                  </div>
              </div>
            </div>
        </div>
        <div class="btn_block">
            <router-link to="/about">
                <button  type="button" class="main_button">Далее</button>
            </router-link>
            <router-link to="/map">
                <button  type="button" class="main_button">map</button>
            </router-link>
                <button  type="button" class="main_button" style="background: red;" @click="check">Далее</button>
        </div>
    </div>
</template>

<script>
import { useVuelidate } from '@vuelidate/core'
import { required, email } from '@vuelidate/validators'


export default {
  setup () {
    return { v$: useVuelidate() } 
  },
   data () {
    return {
      name: '',
      name2: '',
      email: '',
      list: [],
      filter: "",
      filterList: "",
      isHide: true,
    }
  },
  validations () {
    return {
      name: { required },
      name2: { required },
      email: { required, email },
    }
  },
  methods: {

    // выделение текста фильтра
    matchName(current) {
      let reggie = new RegExp(this.filter, "ig");
      let found = current.search(reggie) !== -1;
      return !found ? current : current.replace(reggie, '<span class="red">' + this.filter + '</span>');
    },
    // акардион 
    open () {
      this.isHide = !this.isHide
    },
    ApiTest() {
      setTimeout(async() =>{
      const f = await fetch('https://dev6.automatisation.art/dev/sputniki/api/roles')
      const data = await f.json();
      console.log(data);
    }, 5);
    },
    FilterClick(t) {
      this.filterList = t.name
    },
    // заполнение api по порядку
    JastFilter () {
      setTimeout(async() =>{
        const f = await fetch('https://jsonplaceholder.typicode.com/photos');
        const data = await f.json();
        // for (let i = 0;i <= data.length; i++) {
        for (let i = 0;i <= 200; i++) {
          const newRequest = {
          name: data[i].title,
          url: data[i].url
          };
        this.list.push(newRequest);
        }  
      }, 5);  
      },
      // фильтер
      filteredList() {
      return this.list.filter(list => (list.name.toLowerCase().includes(this.filter.toLowerCase())) || (list.url.toLowerCase().includes(this.filter.toLowerCase())))
      },
      // роут по условию
      check() {
        setTimeout(async() =>{
          const f = await fetch('https://jsonplaceholder.typicode.com/users');
          const data = await f.json();
          if (data[1].name != null) {
            this.$router.push("/part");
          }
        }, 5)

      },
  },
  beforeMount(){
        this.JastFilter ();
        this.ApiTest();
  }
}
</script>

