import { createRouter, createWebHashHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import about from '../views/AboutView.vue'
import api from '../views/RestApi.vue'
import chat from '../views/MyChats.vue'
import part from '../views/MyPart.vue'


const router = createRouter({
  history: createWebHashHistory(),
  routes: [
    {
      path: '/',
      name: 'home',
      component: HomeView
    },
    {
      path: '/about',
      name: 'about',
      component: about,
    },
    {
      path: '/api',
      name: 'api',
      component: api
    },
    {
      path: '/chat',
      name: 'chat',
      component: chat
    },
    {
      path: '/part',
      name: 'part',
      component: part
    },
    {
      path: '/tg',
      name: 'tg',
      component: () => import('../views/TgTest.vue')
    },
    {
    path: '/map',
    name: 'map',
    component: () => import('../views/MyMap.vue')
    },
  ]
})

export default router
